import React from 'react';
import data from './colleges.json';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      data:[],
      current:[],
      loading:false,
      count:10,
      hasMoreData:true
    }
   }

  componentDidMount(){
   this.setState({data:data.colleges,current:data.colleges.slice(0, this.state.count)});

   window.addEventListener('scroll', () => {
    if (window.innerHeight + document.documentElement.scrollTop !== document.documentElement.offsetHeight)
    return;

    this.loadMoreColleges();
  });  
   }

   componentWillUnmount() {
    window.removeEventListener('scroll');
}


   loadMoreColleges=()=>{
    if(this.state.count >= this.state.data.length){
      this.setState({ hasMoreData: false });      
    } else {
    this.setState({ loading: true });
    let c = this.state.count+10;
    let data = this.state.data.slice(0, c);
    setTimeout(() => {
        this.setState({ current:data, count:c, loading: false, hasMoreData:true });
    }, 1000);
  }
   }


render(){
  const colleges = this.state.current;
  console.log(colleges);
  return (
    <div className="container" >
    <h3 className="heading">Colleges in North India</h3>
    <div className="wrapper">
    {colleges.length > 0 ?
    colleges.map((college, i)=>(
      <div key={i} className="college">
        <div className="college-image-wrap">
         {college.promoted ?
          <div className="label">            
           <span> PROMOTED </span>
          </div> : null}

          <div className="rating">
           <span><strong>{college.rating}</strong>/5 </span>
           <span>{college.rating_remarks}</span>
          </div>

          <div className="tags-bar">
          <div className="tags">
            {college.tags.length > 0 ?
             college.tags.map((tag, i)=>(
             <span key={i} >{tag}</span>
             )) : null}
             </div>
             <div className="rank">
               <p>{college.ranking}</p>
             </div>
          </div>
        </div>

        <div className="college-description">
          <div className="wrapper">
          <div className="desc">
            <h3>{college.college_name}  </h3>
            <p className="addr">
            {college.nearest_place.length > 0 ?
             college.nearest_place.map((place, i)=>(
             <span key={i} >{place}</span>
             )) : null}
            </p>
            <p className="addr2">
              <span>93% match : </span> 
              {college.famous_nearest_places}
              {/* <span>2.5kms </span> from gtb nagar, <span>7kms</span> from rajiv chowk  */}
            </p>
          </div>
          <div className="desc2">
           <div className="price"> 
           <span>Rs {college.original_fees} </span> 
           <span className="off-tag">
             <span className="dot"></span>
             <span className="val">{college.discount} </span>
           </span>
           </div>            
           <span className="actual-price">
             Rs {college.discounted_fees}
           </span>
           <span className="det"> {college.fees_cycle}</span>
          </div>
          </div>
          <div className="desc3">
          <p className="flat">
            {college.offertext}
              {/* flat Rs <span>2,000</span> + upto Rs <span>500</span> wallet ! to avail... 
              <span> LOGIN </span> */}
            </p>

          <p className="cancle">
            {college.amenties.length > 0 ?
             college.amenties.map((an, i)=>(
             <span key={i} >{an}</span>
             )) : null}
             </p>  
          </div>
        </div>
      </div> ))
      : 
      <div>
        <h3>Wait for College ...</h3>
      </div> }
    </div>
    {this.state.loading ? <div style={{textAlign:'center'}}>Loading ...</div>: null}
  </div>
  );
}
}


export default App;
